import React from 'react';
import logo from './logo.svg';
import './App.css';

import { styled } from '@mui/material/styles';
import { Button, Grid, Typography } from '@mui/material';
import { StandardTextFieldProps, TextField } from '@mui/material';
import Modal from '@mui/material/Modal';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';

import { AbiItem, TransactionReceipt } from 'ethereum-abi-types-generator';

import ConnectButton from "./components/ConnectButton";

import busdfarm from "./assets/img/busd-logo-medium.png";
import busdfarmtext from "./assets/img/busdfarm-header-text.png";
import diamondteamlogo from "./assets/img/diamond-team-logo.png";

import {ContractContext, ContractInfoResponse, PayoutOfResponse, PoolTopInfoResponse, UserInfoResponse, UserInfoTotalsResponse} from './assets/abi/farm-contract';
import Web3 from "web3";
import copy from 'clipboard-copy';
import { formatEthFromWei, formatNumber } from './Formatters';
import {ReactComponent as TelegramIcon} from './assets/img/Telegram_logo.svg';
import {ReactComponent as BUSDIcon} from './assets/img/busd.svg';
import ShieldIcon from '@mui/icons-material/VerifiedUser';

//
//  Global setup Web3 Contract-Interaction
//
import abi from "./assets/abi/farm-contract.json";

const contractAddress: string = '0x0f0b34db29e721dec954457243af1e0478f562ac'; // release on testnet

//const contractAddress: string = '0x4e57f68b3d38b6413de223ea2d613e2c756307d5'; // release on mainnet


declare global {
  interface Window {
    dataRefreshInterval: number,
    farm_contract: ContractContext;
    web3: Web3;
    ethereum: any;
    contractAddress: string;
    contract_abi: AbiItem[]
    walletAddress: string;
    connected: boolean;
    refferalAddress: string; // the ref address that invited me
    allowance: number;
  }
}

interface AppProps {
  style?: React.CSSProperties;
}

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.h3,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const modalStyle = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "70%",
  height: "60%",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  color: "lightgrey",
  overflow: "auto",
  opacity: 0.95
};

window.dataRefreshInterval = 3000;

class App extends React.Component<AppProps> {
  
  state = {
    modalOpen: false,
    referralFromUrl: "",
    totalDeposits: "",
    withdrawnByMembers: "",
    topSponsorRewardPool: "",
    topSponsorRewardPoolCountdown: "",
    maximumAmountFromTopReferrer: "",
    referredBy: "?",
    yourAddress: "?",
    nextIncomeCountdown: "00:00:00",
    incomeLimitRemain: "",
    dailyIncome: "",
    myCurDeposit: "",
    myCurWithdraw: "",
    directReferralIncome: "",
    matchingBonus: "",
    topSponsorRewards: "",
    unwithdrawnIncome: "",
    totalAmountOfDepositedBNB: "",
    totalAmountOfReceivedBNB: "",
    totalReturnOfInvestment: "",
    personalInvitedPartners: "0",
    totalNumberOfPartners: "0",
    allDownlinesInvestments: "",
    projectWallet: ""
  }
  private amountInput: React.RefObject<StandardTextFieldProps>;
  private sponsorInput: React.RefObject<StandardTextFieldProps>;

  baseState: any;
  _stateInterval: any;

  constructor(props: AppProps) {
    super(props);

    this.baseState = this.state;
    // properties

    this.amountInput = React.createRef();
    this.sponsorInput = React.createRef();
  }

  private _modalInterval: any = null;

  componentDidMount = () => { 
    window.contractAddress = contractAddress;
    window.contract_abi = abi as AbiItem[];
    
  
    const urlParams = new URLSearchParams(window.location.search);
    let refAddress = urlParams.get('ref');
    if(refAddress != null){
      window.refferalAddress = refAddress;
      this.setState({
        referralFromUrl: "referral bonus for your invest goes to " + window.refferalAddress
      });
    }    

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval + window.dataRefreshInterval);

    // let unlock = urlParams.get('unlock');
    // if(unlock != "true"){
    //   this._modalInterval = setInterval(this.handlePhase2Modal, 1000);
    // }    
    //this.setModalOpen();
  }


  componentWillUnmount = () => { 
    
  }

  checkState = () => {  

    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }

    this.setState({
      yourAddress: window.walletAddress,
    })


     window.farm_contract.methods.project().call().then((result: string) => {
      if(!window.web3.utils.isAddress(this.state.projectWallet)){
        this.setState({
          projectWallet: result
        });
      }
    }).catch((err:any) => {
      console.log(err);
    }); 
    


    // contract info
    window.farm_contract.methods.contractInfo().call().then((result: ContractInfoResponse) => {

      let topSponsorRewardPoolCountdown = new Date((parseInt(result._pool_last_draw)+86400) *1000  );

      this.setState({
        totalDeposits: result._total_deposited,
        withdrawnByMembers: result._total_withdraw,
        topSponsorRewardPoolCountdown: topSponsorRewardPoolCountdown.toLocaleString(),
        maximumAmountFromTopReferrer: result._pool_lider
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // poolTopInfo
    window.farm_contract.methods.poolTopInfo().call().then((result: PoolTopInfoResponse) => {
      this.setState({
        topSponsorRewardPool: result.deps[0].toString()
        
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // userInfo
    window.farm_contract.methods.userInfo(window.walletAddress).call().then((result: UserInfoResponse) => {
      let nextIncomeCountdown = result.deposit_time;
      if(nextIncomeCountdown == "0"){
        nextIncomeCountdown = "Will show after deposit";
      }else{
        nextIncomeCountdown = new Date((parseInt(result.deposit_time) + 86400 ) * 1000).toLocaleString();
      }

      this.setState({
        referredBy: result.upline == "0x0000000000000000000000000000000000000000"? "Will show after deposit": result.upline,
        nextIncomeCountdown: nextIncomeCountdown,
        myCurDeposit: result.deposit_amount,
        myCurWithdraw: result.payouts,
        directReferralIncome: result.direct_bonus,
        matchingBonus: result.match_bonus,
        topSponsorRewards: result.pool_bonus,
        unwithdrawnIncome: parseFloat(window.web3.utils.fromWei(result.direct_bonus)) + parseFloat(window.web3.utils.fromWei(this.state.dailyIncome)) + parseFloat(window.web3.utils.fromWei(result.match_bonus)) + parseFloat(window.web3.utils.fromWei(result.pool_bonus))
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // payoutOf
    window.farm_contract.methods.payoutOf(window.walletAddress).call().then((result: PayoutOfResponse) => {

      this.setState({
        incomeLimitRemain: 3 * parseFloat(window.web3.utils.fromWei(this.state.myCurDeposit)) - parseFloat(window.web3.utils.fromWei(this.state.myCurWithdraw)),
        dailyIncome: result.payout
      });
    }).catch((err:any) => {
      console.log(err);
    });

    // userInfoTotals
    window.farm_contract.methods.userInfoTotals(window.walletAddress).call().then((result: UserInfoTotalsResponse) => {

      this.setState({
        totalAmountOfDepositedBNB: result.total_deposits,
        totalAmountOfReceivedBNB: result.total_payouts,
        totalReturnOfInvestment: parseFloat(window.web3.utils.fromWei(result.total_payouts)) / parseFloat(window.web3.utils.fromWei(result.total_deposits)) * 100,
        personalInvitedPartners: result.referrals,
        allDownlinesInvestments: result.total_downline_deposit,
        totalNumberOfPartners: result.total_structure
      });
    }).catch((err:any) => {
      console.log(err);
    });


  }


  setModalOpen = () => {
    this.setState({modalOpen: true});
  }

  setModalClose = () => {
    clearInterval(this._modalInterval);
    this._modalInterval = null;
    this.setState({modalOpen: false});
  }

  copyRefToClipboard = () => {
    let refAddress = window.location.origin + "?ref=" + window.walletAddress;
    copy(refAddress);
  }

  onClick_VerifiedContract = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://testnet.bscscan.com/address/"+ window.contractAddress +"#code";
    a.click();
  }

  onClick_Telegram = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://t.me/joinchat/qf0n194WaYljMjI0";
    a.click();
  }

  onClick_Audit = () => {
    let a = document.createElement("a");
    a.target = "_blank";
    a.href = "https://cybercrimeshield.org/secure/mydiamondteam/";
    a.click();
  }

  onClick_Invest = () => {
    if(!this.amountInput.current || !this.sponsorInput.current){
      return;
    }


      let ref = window.refferalAddress;
      if (!window.web3.utils.isAddress(ref)){
        ref=this.sponsorInput.current.value as string;
        if(!window.web3.utils.isAddress(ref) && ref.length > 0){
          alert("invalid sponsor address");
          return;
        }
      }
      if(ref.length == 0){
        ref = this.state.projectWallet;
        console.log("invest with project wallet as sponsor");
      }
  
      // window.farm_contract.methods.checkUplineValid(window.walletAddress, ref).call().then((result: boolean) => {
  
      //  if(result == true){
          if(!this.amountInput.current){
            return;
          }
  
          window.farm_contract.methods.deposit( ref )
          .send({ value: window.web3.utils.toWei(this.amountInput.current.value as string), from:window.walletAddress}).then((response:any) => {
      
          }).catch((err:any) => {
            console.log(err)
          });
        // }else{
        //   alert("invalid sponsor address");
        // }
  
  
      // }).catch((err:any) => {
      //   console.log(err)
      // });  
    

  }

  onClick_Reinvest = () => {
    window.farm_contract.methods.reinvest().send({from: window.walletAddress}).then((result: TransactionReceipt) => {
      alert("reinvest transaction submitted")
    }).catch((err:any) => {
      console.log(err)
    });
  }

  onClick_Withdraw = () => {
    window.farm_contract.methods.withdraw().send({from: window.walletAddress}).then((result: TransactionReceipt) => {
      alert("withdraw transaction submitted")
    }).catch((err:any) => {
      console.log(err)
    });
  }

  //  Main Layout
  //
  render = () => {
    return (
      <Box sx={{ flexGrow: 1 }} style={{width: "80%", margin: "auto", marginTop: "32px"}}>
        <Grid id="mainApp" container spacing={2}>

          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px"}}>
              <img style={{width: "100%"}} src={diamondteamlogo}/>
          
              <ConnectButton />

              <Typography sx={{ fontSize: 16 }} color="text.secondary" gutterBottom>
                referral link
              </Typography>
              <Typography sx={{ fontSize: 16 }} style={{wordBreak: "break-all"}} color="text.primary" gutterBottom>
                {window.location.origin}?ref=<span id={"walletAddressRefLink"}></span><br/>
                <Button color={"secondary"} variant={"contained"} sx={{fontSize: 12}} onClick={this.copyRefToClipboard}>copy</Button>              
              </Typography>

              <Typography sx={{ fontSize: 12, color: "orange" }} color="text.secondary" gutterBottom>
                {this.state.referralFromUrl}
              </Typography>

              <Grid container>
                <Grid item xs={12} md={6}>
                  <Button variant={"outlined"} sx={{my: 1}} size="small" onClick={this.onClick_VerifiedContract}><BUSDIcon style={{height: "30px", marginRight: "8px"}}></BUSDIcon>Verified Contract</Button>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Button variant={"outlined"} sx={{my: 1}} size="small" onClick={this.onClick_Telegram}><TelegramIcon style={{height: "30px", marginRight: "8px"}}></TelegramIcon>MyDeFi-Team Telegram</Button>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Button variant={"outlined"} sx={{my: 1}} size="small" onClick={this.onClick_Audit}><ShieldIcon style={{height: "30px", marginRight: "8px"}}></ShieldIcon>Security Audit</Button>
                </Grid> 
              </Grid>
            </Paper>
          </Grid>

          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 16 }} color="primary" gutterBottom>
                join now
              </Typography>
              <Typography variant="h2">
                <TextField inputRef={this.sponsorInput} label={"your sponsor"} type="text" fullWidth={true}></TextField>
              </Typography>
              <Typography variant="h2">
                <TextField inputRef={this.amountInput} label={"amount"} type="number" fullWidth={true}></TextField>
              </Typography>
              <Button sx={{my: 1}} disabled={false} size="large" variant="contained" onClick={this.onClick_Invest} fullWidth={true}>deposit</Button>


              <Typography sx={{ fontSize: 16 }} color="primary" gutterBottom>
                smart contract statistics
              </Typography>


              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Total BNB Deposits All Time
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.totalDeposits, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Amount Withdrawn By Members
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.withdrawnByMembers, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Top Sponsor Reward Pool
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.topSponsorRewardPool, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Next Top Sponsor Pool Reward
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {this.state.topSponsorRewardPoolCountdown}
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Maximum Amount From Top Referrer
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {formatEthFromWei(this.state.maximumAmountFromTopReferrer, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Referred By
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {this.state.referredBy}
              </Typography>

            </Paper>
          </Grid>

      
          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 16 }} color="primary" gutterBottom>
                personal statistics
              </Typography>


              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Your Address
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                {this.state.yourAddress}
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                My deposits
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.myCurDeposit, 6)} BNB
              </Typography>
            
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Next Income
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {this.state.nextIncomeCountdown}
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                300% Income Limit Remain
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.incomeLimitRemain, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Daily Income 1.5%
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.dailyIncome, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Direct Referral Income
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.directReferralIncome, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Matching Bonus
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.matchingBonus, 6)} BNB
              </Typography>

              
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                TOP Sponsor Rewards
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.topSponsorRewards, 6)} BNB
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Income Withdrawn To Wallet
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.myCurWithdraw, 6)} BNB
              </Typography>      
              
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Unwithdrawn Income
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.unwithdrawnIncome, 6)} BNB
              </Typography>      

              <Button sx={{my: 1}} disabled={false} onClick={this.onClick_Withdraw} size="large" variant="contained"fullWidth={true}>withdraw</Button>
              <Button sx={{my: 1}} disabled={false} onClick={this.onClick_Reinvest} size="large" variant="contained"fullWidth={true} color={"secondary"}>reinvest</Button>

            </Paper>
          </Grid>
    
          <Grid item xs={12} md={3}>
            <Paper className={"minHeight"} style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 16 }} color="primary" gutterBottom>
                statistics
              </Typography>


              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Amount Of Deposited BNB
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.totalAmountOfDepositedBNB, 6)} BNB
              </Typography>  

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Amount Of Received BNB
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.totalAmountOfReceivedBNB, 6)} BNB
              </Typography>  

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Return on Investment
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatNumber(this.state.totalReturnOfInvestment, 6)} BNB
              </Typography>  



              <Typography sx={{ fontSize: 16 }} color="primary" gutterBottom>
                organization
              </Typography>

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Personal Invited Partners
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {this.state.personalInvitedPartners}
              </Typography> 

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              Total Number Of Partners
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {this.state.totalNumberOfPartners}
              </Typography>   

              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              All downline’s investments
              </Typography>
              <Typography sx={{ fontSize: 14 }} color="text.primary" gutterBottom>
                  {formatEthFromWei(this.state.allDownlinesInvestments, 6)} BNB
              </Typography>  

            </Paper>
          </Grid>

          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
              The principal deposit cannot be withdrawn, only the retuns and referral rewards are paid out. Check the Transactions on Contract and Contract Source. High Risk and High Rewards! DYOR!
              </Typography>
            </Paper>
          </Grid>
          <Grid item xs={12} md={12}>
            <Paper style={{padding: "16px"}}>
              <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                fees fees fees!<br/>
                ... and 15 levels
              </Typography>
            </Paper>
          </Grid>
        </Grid>
        

        <Modal
          open={this.state.modalOpen}
         /*  onClose={this.setModalClose} */
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
          disableEscapeKeyDown={false}

        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
            hello
            </Typography>
            <Typography id="modal-modal-description" sx={{ mt: 2, fontSize: 14 }}>
we are not live yet
            </Typography>
            <Button sx={{my: 1}} size="small" fullWidth={true} variant={"contained"} onClick={this.setModalClose}>close</Button>
          </Box>
        </Modal>
      </Box>
    );
  }
}

export default App;
